<template>
  <div class="login">
      <form >
          <div class="form-group col-lg-3 col-sm-6">
            <input v-model='email' type='email' placeholder="email" size="50" required aria-label="Email de connection"> <br>
          </div>
          <div class="form-group col-lg-3 col-sm-6">
            <input v-model='password' type='password' placeholder="password" size="50" required> <br>
          </div>
          <button @click.prevent='login()'>Se connecter</button>
          <p>Pas encore inscrit ? <a href="#">Inscrivez-vous</a></p>
      </form> 
  </div>
</template>



<script>

import axios from 'axios'

export default {
  name: 'login',
  
  data() {
      return {
          email:'',
          password:'',
          userId:'',
      }
  },
  methods: {
      login : function () {
          let token = "";
          if(this.email=="" || this.password==""){
              alert ("Veuillez renseigner un email et un mot de passe correct !")
          } else {
              axios.post('http://localhost:8080/api/users/login', {
                  email: this.email,
                  password: this.password,
              },
              {
                  headers: {
                    'Content-type': 'application/json',
                    'Authorization': `Bearer${token}`
                  }
              })
              .then( (response) => {
                //   console.log(response);
                  localStorage.setItem('userId', response.data.userId)
                  localStorage.setItem('token', response.data.token)
                  
                     location.href = "http://localhost:8080/api/mur";
                      
                        
              })
              .catch(error => {error.status(400).json({message: 'Something went wrong'});
            })
          }
      },
  }  
}
</script>

