<template>
  <div class="signup">
      <form>
        <input v-model='name' type='name' placeholder="name" size="50" required ><br>
        <input v-model='email' type='email' placeholder="email" size="50" required aria-label="Email de connection"> <br>
        <input v-model='password' type='password' placeholder="password" size="50" required> <br>
      </form>  

        <button @click="signup()" type="submit" aria-label="S'inscrire" formaction="" formenctype="application/x-www-form-urlencoded">S'inscrire</button>

    
  </div>
</template>

<script>
export default {
  name: 'signup',
  
  data() {
      return {
          name:'',
          email:'',
          password:'',
      }
  },
  methods: {
    signup() {
        const data = {
              name: this.name,
              email: this.email,
              password: this.password
          };

          fetch("http://localhost:8080/api/users/signUp", {
              method: "POST",
              headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
              },
              body: JSON.stringify(data)
          })
           .then( response => {
        if(response.ok) {
          return response.json()
        } else {
          return response.text()
          .then((text) => {
            throw new Error(text)}
          )
        }
      })  
        
      .then((value) => {
        const token = JSON.stringify(value.token);
        const userId = JSON.stringify(value.userId);
        //const role = JSON.stringify(value.role);
        localStorage.setItem("userToken", token);
        localStorage.setItem("userId", userId);
        //localStorage.setItem("role", role);
        
      })
      .catch(alert)
       
      }
  }
}
  
  
</script>

